.nav {
  display: flex;
  color: #676fa3;
  font-size: 1.6rem;
  justify-content: space-between;
  align-items: center;
  height: 8vh;

  background-color: var(--color-navbar);
  position: relative;

  .logo {
    width: 4rem;
    height: 4rem;
  }
  &__links {
    display: flex;
    @media screen and (max-width: 768px) {
      position: absolute;
      right: 0;
      top: 8vh;
      flex-direction: column;
      width: 0%;
      background-color: var(--color-navbar);
      height: 92vh;
      justify-content: space-evenly;
      align-items: center;
      transition: width 2s;
    }
  }
  &__items {
    list-style-type: none;
    margin-right: 2rem;
    @media screen and (max-width: 768px) {
      display: none;
    }
  }
  &__link:link,
  &__link:visited {
    text-decoration: none;
    color: inherit;
  }
  .burger-menu {
    // display: flex;
    display: none;
    flex-direction: column;
    align-items: center;
    width: 2.5rem;
    position: absolute;
    right: 2%;
    @media screen and (max-width: 768px) {
      display: flex;
    }
  }
  .line {
    justify-content: center;
    height: 0.5rem;
    background-color: red;
    width: 100%;
    margin: 0.125rem;
    border-radius: 1rem;
    transition: transform 0.5s ease;
  }

  .rotate .line--2 {
    display: none;
  }
  .rotate .line--1 {
    transform: translateY(0.7rem) rotate(45deg);
  }
  .rotate .line--3 {
    transform: rotate(-45deg);
  }
  .show-menu {
    @media screen and (max-width: 768px) {
      width: 50%;
      .nav__items {
        display: flex;
      }
    }
  }
}
